.news
{
	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& h1,
	& h2,
	& h3,
	& h4,
	& h5,
	& h6
	{
		font-family: $font-montserrat;
		font-size: 45px;
		font-weight: 700;
		line-height: 50px;

		margin-top: 0;
		margin-bottom: 50px;

		text-transform: uppercase;

		color: $color-light-gray;

		@media screen and (max-width: 540px)
		{
			font-size: 24px;
			line-height: 32px;
		}
	}

	& p
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;

		max-width: 985px;
		margin-top: 0;
		margin-bottom: 50px;

		color: rgba($color-light-gray, .8);
	}

	& img
	{
		display: inline-block;

		width: 100%;
		max-width: 985px;
		height: auto;
		margin-bottom: 50px;
	}

	& .social
	{
		@media screen and (max-width: 992px)
		{
			align-self: flex-start;
			flex-direction: column;

			&__btn
			{
				margin-bottom: 10px;
			}
		}
	}
}
