.decision
{
	display: flex;
	flex-direction: column;

	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		margin-bottom: 50px;
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 30px;
			line-height: 36px;
		}
	}
}
