.article
{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	max-width: 890px;

	@media screen and (max-width: 1440px)
	{
		max-width: 670px;
	}

	@media screen and (max-width: 1200px)
	{
		flex-direction: column;

		max-width: 100%;
	}

	& h1,
	& h2,
	& h3,
	& h4,
	& h5,
	& h6
	{
		font-family: $font-montserrat;
		font-size: 20px;
		font-weight: 700;
		line-height: 28px;

		margin-top: 0;
		margin-bottom: 50px;

		text-transform: uppercase;

		color: $color-dark-gray;
	}

	& p
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;

		margin-top: 0;
		margin-bottom: 30px;

		color: rgba($color-light-gray, .8);
	}

	& img
	{
		display: inline-block;

		margin-bottom: 50px;

		@media screen and (max-width: 1200px)
		{
			width: 100%;
			height: auto;
		}

		&.for-test
		{
			max-width: 440px;
		}
	}
}
