.news-item
{
	display: flex;
	justify-content: space-between;

	margin-bottom: 50px;

	@media screen and (max-width: 992px)
	{
		flex-direction: column;
	}

	@media screen and (max-width: 540px)
	{
		margin-bottom: 25px;
	}

	&:last-of-type
	{
		margin-bottom: 0;
	}

	&__image-wrapper
	{
		flex-shrink: 0;

		width: 100%;
		max-width: 685px;

		@media screen and (max-width: 1440px)
		{
			max-width: 585px;
		}

		@media screen and (max-width: 1440px)
		{
			max-width: 450px;
		}

		@media screen and (max-width: 992px)
		{
			max-width: 100%;
			margin-bottom: 20px;
		}
	}

	&__image
	{
		display: block;

		width: 100%;
		height: auto;
	}

	&__wrapper
	{
		display: flex;
		flex-direction: column;
		justify-content: center;

		padding-left: 50px;

		@media screen and (max-width: 992px)
		{
			padding-left: 0;
		}
	}

	&__title
	{
		font-family: $font-open-sans;
		font-size: 20px;
		font-weight: 700;
		line-height: 28px;

		margin-top: 0;
		margin-bottom: 50px;

		letter-spacing: .8px;
		text-transform: uppercase;

		color: #1d1d20;

		@media screen and (max-width: 540px)
		{
			margin-bottom: 25px;
		}
	}

	&__text
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;

		margin-top: 0;
		margin-bottom: 50px;

		color: rgba($color-light-gray, .8);

		@media screen and (max-width: 540px)
		{
			margin-bottom: 25px;
		}
	}

	&__details
	{
		font-family: $font-open-sans;
		font-size: 16px;
		font-weight: 700;
		line-height: 28px;

		transition-duration: .2s;
		text-decoration: none;
		letter-spacing: .64px;
		text-transform: uppercase;

		color: $color-orange;

		&:hover
		{
			color: rgba($color-orange, .8);
		}
	}
}
