.exhauster
{
	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		align-self: center;

		max-width: 1050px;
		margin-bottom: 50px;
	}

	& .btn
	{
		align-self: center;
	}

	&__item
	{
		position: relative;

		display: flex;
		flex-direction: column;

		min-height: 294px;
		margin-bottom: 50px;

		@media screen and (max-width: 1440px)
		{
			min-height: auto;
		}
	}

	&__image
	{
		position: absolute;
		z-index: 10;
		top: 0;
		left: 50%;

		display: block;

		width: 100%;
		max-width: 520px;
		height: auto;

		transform: translateX(-50%);

		@media screen and (max-width: 1440px)
		{
			position: relative;
		}
	}

	&__advantages-list
	{
		display: flex;
		flex-direction: column;
		flex-grow: 1;
		justify-content: space-between;

		padding-top: 25px;
		padding-bottom: 25px;

		@media screen and (max-width: 1440px)
		{
			display: none;
		}
	}

	&__advantages-item
	{
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__advantages-text
	{
		font-family: $font-open-sans;
		font-size: 16px;
		font-weight: 400;
		line-height: 24px;

		max-width: 300px;

		color: rgba($color-dark-gray, .8);

		&:first-of-type
		{
			text-align: right;
		}
	}

	&__advantages-decor
	{
		position: relative;
		z-index: 1;

		flex-shrink: 0;

		width: 100%;
		max-width: 720px;
		height: 2px;
		margin-right: 25px;
		margin-left: 25px;

		background-color: $color-orange;

		&::before,
		&::after
		{
			position: absolute;
			top: 50%;

			width: 16px;
			height: 16px;

			content: "";
			transform: translateY(-50%);

			border: 2px solid $color-orange;
			border-radius: 50%;
			background-color: $color-white;
		}

		&::before
		{
			left: 0;
		}

		&::after
		{
			right: 0;
		}
	}
}
