.authors
{
	display: flex;

	margin-right: -8px;
	margin-left: -8px;

	&__item
	{
		display: flex;
		align-items: center;

		margin-right: 8px;
		margin-left: 8px;
	}

	&__wrapper
	{
		display: flex;
		flex-direction: column;
	}

	&__image
	{
		max-width: 100px;
		height: auto;
		margin-right: 20px;
	}

	&__name
	{
		font-family: $font-open-sans;
		font-size: 20px;
		font-weight: 700;
		line-height: 28px;

		margin-bottom: 10px;

		letter-spacing: .8px;

		color: #1d1d20;
	}

	&__position
	{
		font-family: $font-open-sans;
		font-size: 14px;
		font-weight: 400;
		line-height: 18px;

		max-width: 170px;

		letter-spacing: .14px;

		color: rgba($color-light-gray, .8);
	}
}
