.articles-filter
{
	margin-bottom: 1px;

	border-bottom: 1px solid rgba($color-dark-gray, .2);

	&__btn
	{
		font-size: 14px;
		font-weight: 400;
		line-height: 28px;

		margin-right: 8px;
		margin-left: 8px;
		padding-bottom: 20px;

		text-transform: uppercase;

		color: $color-dark-gray;
		border: none;
		background-color: transparent;

		@media screen and (max-width: 540px)
		{
			font-size: 12px;
		}

		&.active
		{
			font-weight: 700;

			position: relative;

			&::before
			{
				position: absolute;
				bottom: -3px;
				left: 0;

				width: 100%;
				height: 5px;

				content: "";

				background-color: $color-orange;
			}
		}

		&:first-of-type
		{
			margin-left: 0;
		}

		&:last-of-type
		{
			margin-right: 0;
		}
	}
}
