.article-list
{
	display: flex;
	flex-direction: column;

	width: 100%;
	max-width: 430px;

	border-radius: 5px;

	@media screen and (max-width: 1200px)
	{
		margin-bottom: 50px;
	}

	&__wrapper
	{
		display: none;

		border: 1px solid #d6d6d6;
		border-radius: 5px;
	}

	&__item
	{
		margin-bottom: 1px;
	}

	&__item.active &__wrapper
	{
		display: block;
	}

	&__title
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 700;

		display: block;

		width: 100%;
		padding-top: 21px;
		padding-right: 30px;
		padding-bottom: 21px;
		padding-left: 30px;

		cursor: pointer;
		letter-spacing: .28px;
		text-transform: uppercase;

		color: $color-white;
		border-radius: 5px;
		background-color: $color-orange;
		box-shadow: 0 0 16px rgba(223, 40, 40, .1);
	}

	&__link
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 400;

		display: block;

		padding-top: 21px;
		padding-right: 30px;
		padding-bottom: 21px;
		padding-left: 30px;

		text-decoration: none;
		letter-spacing: .28px;

		color: $color-light-gray;
		border-bottom: 1px solid rgba($color-light-gray, .05);
	}
}
