.articles-item
{
	position: relative;

	overflow: hidden;
	flex-basis: 425px;
	flex-shrink: 0;

	margin-bottom: 50px;

	border-radius: 5px;

	@media screen and (max-width: 1200px)
	{
		flex-basis: 300px;
	}

	@media screen and (max-width: 992px)
	{
		flex-basis: 350px;
	}

	@media screen and (max-width: 768px)
	{
		flex-basis: 100%;
	}

	@media screen and (max-width: 768px)
	{
		margin-bottom: 25px;
	}

	&::before
	{
		display: block;

		padding-bottom: 100%;

		content: "";
	}

	&__image
	{
		position: absolute;
		top: 0;
		left: 0;

		display: block;

		width: auto;
		height: 100%;
	}

	&__filter
	{
		font-size: 14px;
		font-weight: 700;
		line-height: 28px;

		position: absolute;
		top: 40px;
		right: 0;

		width: 215px;
		padding-top: 14px;
		padding-bottom: 14px;

		text-align: center;
		letter-spacing: .28px;
		text-transform: uppercase;

		color: $color-white;
		border-top-left-radius: 5px;
		border-bottom-left-radius: 5px;
		background-color: $color-orange;
	}

	&__desc
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;

		position: absolute;
		bottom: 0;
		left: 0;

		min-height: 115px;
		margin-top: 0;
		margin-bottom: 0;
		padding: 30px;

		color: $color-white;
		background-color: $color-dark-gray;
	}
}
