.hamburger-menu
{
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;

	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;

	width: 100%;
	height: 100%;

	color: $color-white;
	background-color: #333;

	& .menu__link,
	& .callback__link
	{
		color: $color-white;
	}

	& .menu__list
	{
		flex-direction: column;

		margin-bottom: 30px;
	}

	& .menu__item
	{
		margin-right: 0;
		margin-left: 0;
		padding-top: 10px;
		padding-bottom: 10px;

		text-align: center;
	}

	.mfp-close
	{
		color: $color-white;
	}
}
