.detail-types
{
	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& .title
	{
		text-align: center;
	}

	& .btn
	{
		align-self: center;
	}

	&__list
	{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__item
	{
		position: relative;

		display: flex;
		overflow: hidden;
		align-items: flex-end;

		width: 100%;
		max-width: 415px;
		min-height: 360px;
		margin-bottom: 50px;

		cursor: pointer;

		border-radius: 5px;

		@media screen and (max-width: 1440px)
		{
			max-width: 360px;
			margin-bottom: 25px;
		}

		@media screen and (max-width: 1200px)
		{
			max-width: 310px;
			margin-bottom: 15px;
		}

		@media screen and (max-width: 992px)
		{
			width: 100%;
			max-width: 650px;
			margin-right: auto;
			margin-left: auto;
		}

		&:nth-last-child(-n+2)
		{
			max-width: 645px;

			@media screen and (max-width: 1440px)
			{
				max-width: 560px;
			}

			@media screen and (max-width: 1200px)
			{
				max-width: 475px;
			}

			@media screen and (max-width: 992px)
			{
				max-width: 650px;
			}
		}
	}

	&__item-image
	{
		position: absolute;
		top: 0;
		left: 0;

		width: auto;
		height: 100%;
	}

	&__item-wrapper
	{
		position: relative;
		z-index: 1;

		width: 100%;
		padding-top: 25px;
		padding-bottom: 25px;
		padding-left: 25px;

		border-radius: 5px;
		background-color: rgba($color-white, .8);
	}

	&__item-title
	{
		font-family: $font-open-sans;
		font-size: 20px;
		font-weight: 700;
		line-height: 28px;

		display: block;

		margin-bottom: 15px;

		letter-spacing: .8px;
		text-transform: uppercase;

		color: #1d1d20;
	}

	&__item-list
	{
		display: none;

		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;

		& li
		{
			font-family: $font-open-sans;
			font-size: 18px;
			font-weight: 400;
			line-height: 24px;

			color: rgba($color-light-gray, .8);
		}
	}

	&__item:hover &__item-list
	{
		display: block;
	}
}
