.logo
{
	&__link
	{
		display: flex;
		align-items: center;

		text-decoration: none;
	}

	&__image
	{
		display: block;

		width: 100%;
		max-width: 106px;
		height: auto;
		margin-right: 30px;
	}

	&__desc
	{
		font-size: 14px;
		font-weight: 700;
		line-height: 28px;

		max-width: 130px;

		letter-spacing: .28px;
		text-transform: uppercase;

		color: rgba($color-dark-gray, .8);

		@media screen and (max-width: 1200px)
		{
			display: none;
		}
	}
}
