.contacts
{
	font-style: normal;

	&__list
	{
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;
	}

	&__item
	{
		font-size: 14px;
		font-weight: 700;
		line-height: 28px;

		letter-spacing: .28px;
		text-transform: uppercase;

		color: #1d1d20;

		> span
		{
			transition: .2s;

			color: #df6928;
		}
	}

	&__link
	{
		transition: .2s;
		text-decoration: none;

		color: #1d1d20;
	}

	&__item:hover > span
	{
		color: #1d1d20;
	}

	&__item:hover &__link
	{
		color: #df6928;
	}
}
