.about
{
	background-image: linear-gradient(to right, #fff 0%, #fff 50%, #f7f7f7 50%, #f7f7f7 100%);

	@media screen and (max-width: 992px)
	{
		background-image: none;
	}

	&__inner
	{
		display: flex;
		justify-content: space-between;

		@media screen and (max-width: 992px)
		{
			flex-direction: column;
		}
	}
}

.about-contacts
{
	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 768px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__item
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;

		max-width: 470px;

		color: rgba($color-light-gray, .8);

		span
		{
			font-weight: 700;
		}

		a
		{
			text-decoration: none;
		}
	}

	&__title
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 700;
		line-height: 24px;

		margin-top: 45px;

		text-transform: uppercase;

		color: rgba($color-light-gray, .8);
	}
}

.about-form
{
	display: flex;
	flex-basis: 790px;
	flex-direction: column;
	flex-grow: 0;
	flex-shrink: 0;

	padding: 100px;

	background-color: #f7f7f7;

	@media screen and (max-width: 1200px)
	{
		flex-basis: 510px;
	}

	@media screen and (max-width: 992px)
	{
		flex-basis: 100%;
	}

	@media screen and (max-width: 768px)
	{
		padding: 0;
		padding-bottom: 50px;

		background-color: #fff;
	}

	& .title
	{
		margin-bottom: 5px;
	}

	& .text
	{
		margin-bottom: 30px;
	}

	& form
	{
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	& input
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 400;

		flex-basis: 270px;

		margin-bottom: 20px;
		padding-top: 20px;
		padding-right: 25px;
		padding-bottom: 20px;
		padding-left: 25px;

		color: rgba($color-dark-gray, .8);
		border: 1px solid #bababa;
		border-radius: 5px;

		&::placeholder
		{
			color: rgba($color-dark-gray, .8);
		}
	}

	& textarea
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 400;

		flex-basis: 100%;

		min-height: 170px;
		margin-bottom: 40px;
		padding: 25px;

		resize: none;

		color: rgba($color-dark-gray, .8);
		border: 1px solid #bababa;
		border-radius: 5px;

		&::placeholder
		{
			color: rgba($color-dark-gray, .8);
		}
	}

	& button
	{
		margin-right: auto;
		margin-left: auto;
	}
}
