.labs-servies
{
	overflow: hidden;

	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__inner
	{
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__wrapper
	{
		flex-shrink: 0;

		@media screen and (max-width: 540px)
		{
			flex-shrink: 1;
		}
	}

	& .title
	{
		position: relative;

		min-height: 77px;


		& span
		{
			line-height: 77px;

			position: absolute;
			top: 0;
			left: 0;

			display: inline-block;

			min-width: 600px;

			background-color: $color-white;
		}

		@media screen and (max-width: 540px)
		{
			font-size: 28px;
			line-height: 32px;

			min-height: auto;
		}
	}

	& .text
	{
		max-width: 440px;
	}

	&__decor
	{
		width: 100%;
		max-width: 880px;
		min-height: 400px;

		background-color: #f7f7f7;

		@media screen and (max-width: 992px)
		{
			display: none;
		}
	}
}
