.preview
{
	padding-top: 50px;
	padding-bottom: 40px;

	&__inner
	{
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media screen and (max-width: 768px)
		{
			flex-direction: column;
		}
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 26px;
			line-height: 30px;

			margin-bottom: 20px;
		}
	}
}
