.btn
{
	font-family: $font-montserrat;
	font-size: 14px;
	font-weight: 700;

	padding-top: 10px;
	padding-right: 33px;
	padding-bottom: 10px;
	padding-left: 33px;

	transition-duration: .2s;
	letter-spacing: .28px;
	text-transform: uppercase;

	color: $color-white;
	border: none;
	border-radius: 5px;
	background-color: $color-orange;
	box-shadow: 0 0 16px rgba(223, 40, 40, .1);

	&:hover
	{
		box-shadow: 1px 1px 8px rgba(0, 0, 0, .2);
	}

	&:active
	{
		background-image: radial-gradient(circle 242px at center, #f4732c 0%, #d44b00 100%);
	}
}
