.menu
{
	&__list
	{
		display: flex;

		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;

		border-bottom: 1px solid rgba($color-dark-gray, .2);
	}

	&__item
	{
		position: relative;

		margin-right: 8px;
		margin-left: 8px;
		padding-top: 30px;
		padding-bottom: 30px;

		&:first-of-type
		{
			margin-left: 0;
		}

		&:last-of-type
		{
			margin-right: 0;
		}

		&:hover::before
		{
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;
			height: 5px;

			content: "";
			transform: translateY(50%);

			background-color: $color-orange;
		}
	}

	&__link
	{
		font-size: 14px;

		text-decoration: none;
		text-transform: uppercase;

		&:hover
		{
			font-weight: 700;
		}
	}
}
