.callback
{
	display: flex;
	flex-direction: column;

	&__link
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 700;

		margin-bottom: 13px;

		text-align: center;
		text-decoration: none;
		letter-spacing: .28px;
		text-transform: uppercase;

		color: $color-dark-gray;
	}
}
