.clients-results
{
	overflow: hidden;

	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		align-self: center;

		max-width: 990px;
		margin-bottom: 50px;
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 36px;
			line-height: 40px;
		}
	}

	&__slider
	{
		position: relative;
	}

	&__slider-item
	{
		position: relative;

		display: flex !important;
		overflow: hidden;
		flex-direction: column;

		max-width: 425px;
		padding: 30px;

		border-radius: 5px;

		@media screen and (max-width: 1440px)
		{
			max-width: 350px;
		}

		@media screen and (max-width: 1200px)
		{
			max-width: 470px;
		}

		@media screen and (max-width: 992px)
		{
			max-width: 350px;
		}

		@media screen and (max-width: 768px)
		{
			max-width: 100%;
		}

		&::before
		{
			width: 100%;
			padding-bottom: 100%;

			content: "";
		}
	}

	&__slider-image
	{
		position: absolute;
		top: 0;
		left: 0;

		display: block;

		width: auto;
		min-height: 100%;
	}

	&__slider-logo
	{
		position: absolute;
		z-index: 1;
		top: 30px;
		left: 30px;
	}

	&__slider-desc
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;

		position: absolute;
		z-index: 1;
		bottom: 0;
		left: 0;

		width: 100%;
		padding: 30px;

		color: $color-white;

		@media screen and (max-width: 540px)
		{
			font-size: 16px;
			line-height: 20px;
		}
	}

	& .slick-dots
	{
		bottom: -40px;

		& li
		{
			width: 10px;
			height: 10px;
		}

		& li button
		{
			width: 10px;
			height: 10px;

			border-radius: 50%;
			background-color: rgba($color-orange, .5);

			&::before
			{
				content: none;
			}
		}

		& li.slick-active button
		{
			background-color: $color-orange;
		}
	}
}
