.article-item
{
	padding-top: 100px;
	padding-bottom: 100px;

	@media screen and (max-width: 768px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__inner
	{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	& .social
	{
		margin-bottom: 50px;

		@media screen and (max-width: 992px)
		{
			flex-direction: column;

			&__btn
			{
				margin-bottom: 10px;
			}
		}
	}

	& .authors
	{
		@media screen and (max-width: 768px)
		{
			flex-direction: column;

			&__item
			{
				margin-bottom: 10px;
			}
		}
	}
}
