.social
{
	display: flex;

	margin-right: -8px;
	margin-left: -8px;

	&__btn
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 700;
		line-height: 28px;

		display: block;
		display: flex;
		align-items: center;

		min-height: 50px;
		margin-right: 8px;
		margin-left: 8px;
		padding-right: 20px;
		padding-left: 20px;

		text-decoration: none;

		color: $color-white;
		border-radius: 10px;
		background-color: #1a4482;

		&::before
		{
			display: block;

			margin-right: 20px;

			content: "";
		}

		&--fb
		{
			background-color: #1a4482;

			&::before
			{
				width: 13px;
				height: 25px;

				background-image: url("../images/facebook.png");
			}
		}

		&--tw
		{
			background-color: #55c6f5;

			&::before
			{
				width: 33px;
				height: 26px;

				background-image: url("../images/twitter.png");
			}
		}

		&--in
		{
			background-color: #034ebf;

			&::before
			{
				width: 20px;
				height: 20px;
				margin-top: -5px;

				background-image: url("../images/linkedin.png");
			}
		}
	}
}
