.all-servies
{
	padding-top: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
	}

	&__inner
	{
		display: flex;
		align-items: center;
		flex-direction: column;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		max-width: 920px;
		margin-bottom: 50px;
	}

	&__list
	{
		display: flex;
		flex-wrap: wrap;

		margin-top: 0;
		margin-bottom: 0;
		margin-bottom: 50px;
		padding-left: 0;

		list-style: none;

		border-radius: 10px;
		background-color: $color-orange;
		background-image: url(../images/all-servies-list.jpg);
		background-size: cover;
	}

	&__item
	{
		display: flex;
		align-items: center;
		flex-basis: 25%;
		flex-direction: column;
		justify-content: space-between;

		min-height: 265px;
		padding: 25px;

		border-right: 1px solid $color-white;
		border-bottom: 1px solid $color-white;

		&:nth-child(4n)
		{
			border-right: none;
		}

		&:nth-last-child(-n+4)
		{
			border-bottom: none;
		}

		@media screen and (max-width: 992px)
		{
			padding: 10px;
		}

		@media screen and (max-width: 768px)
		{
			flex-basis: 50%;

			padding: 30px;

			&:nth-last-child(-n+4)
			{
				border-bottom: 1px solid $color-white;
			}

			&:nth-child(4n)
			{
				border-right: 1px solid $color-white;
			}
		}

		@media screen and (max-width: 540px)
		{
			flex-basis: 100%;
		}
	}

	&__desc
	{
		font-family: $font-open-sans;
		font-size: 18	px;
		font-weight: 400;
		line-height: 24px;

		text-align: center;

		color: $color-white;
	}
}
