.laboratory-works
{
	margin-bottom: 100px;

	&__inner
	{
		display: flex;
		align-items: center;
		flex-direction: column;
	}

	& .title
	{
		text-align: center;

		@media screen and (max-width: 768px)
		{
			font-size: 34px;
			line-height: 40px;
		}

		@media screen and (max-width: 540px)
		{
			font-size: 20px;
			line-height: 26px;
		}
	}

	&__wrapper
	{
		display: flex;
		justify-content: space-between;

		margin-bottom: 60px;

		@media screen and (max-width: 992px)
		{
			flex-direction: column;
		}
	}

	&__list
	{
		flex-basis: 49%;

		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;
	}

	&__item
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;

		position: relative;

		margin-bottom: 10px;
		padding-left: 23px;

		color: rgba($color-light-gray, .8);

		&::before
		{
			position: absolute;
			top: 8px;
			left: 0;

			width: 10px;
			height: 10px;

			content: "";

			border-radius: 50%;
			background-color: $color-orange;
		}
	}
}
