.certification
{
	display: flex;
	align-items: center;
	flex-direction: column;

	max-width: 310px;

	&__title
	{
		font-family: $font-open-sans;
		font-size: 16px;
		font-weight: 700;
		line-height: 28px;

		display: block;

		margin-bottom: 30px;

		text-align: center;
		letter-spacing: .64px;
		text-transform: uppercase;

		color: $color-dark-gray;
	}

	&__wrapper
	{
		display: flex;
	}

	&__image
	{
		display: inline-block;

		margin-right: 25px;
		margin-left: 25px;
	}
}
