.form
{
	display: flex;
	justify-content: space-between;

	@media screen and (max-width: 992px)
	{
		flex-direction: column;
	}

	& input,
	& select,
	& label
	{
		flex-basis: 300px;

		@media screen and (max-width: 1440px)
		{
			flex-basis: 250px;
		}

		@media screen and (max-width: 1200px)
		{
			flex-basis: 200px;
		}

		@media screen and (max-width: 992px)
		{
			flex-basis: auto;

			margin-bottom: 10px;
		}
	}

	& input,
	& select,
	{
		font-size: 14px;
		font-weight: 400;

		padding-top: 18px;
		padding-right: 25px;
		padding-bottom: 18px;
		padding-left: 25px;

		color: $color-dark-gray;
		border: 1px solid rgba($color-dark-gray, .3);
		border-radius: 5px;

		&::placeholder
		{
			font-size: 14px;
			font-weight: 400;

			color: $color-dark-gray;
		}
	}

	& select
	{
		position: relative;

		width: 100%;

		appearance: none;
	}

	& .country-select
	{
		position: relative;

		&::before
		{
			position: absolute;
			z-index: 1;
			top: 50%;
			right: 25px;

			width: 14px;
			height: 10px;

			content: "";
			transform: translateY(-50%);

			background-image: url("../images/arrow-to-bottom.png");
		}
	}
}
