.header
{
	&__inner
	{
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media screen and (max-width: 992px)
		{
			padding-top: 15px;
		}

		& > .menu
		{
			@media screen and (max-width: 992px)
			{
				display: none;
			}
		}

		& > .callback
		{
			@media screen and (max-width: 540px)
			{
				display: none;
			}
		}
	}
}
