.container
{
	width: 100%;
	max-width: 1340px;
	margin-right: auto;
	margin-left: auto;

	@media screen and (max-width: 1440px)
	{
		max-width: 1140px;
	}

	@media screen and (max-width: 1200px)
	{
		max-width: 960px;
	}

	@media screen and (max-width: 992px)
	{
		max-width: 720px;
	}

	@media screen and (max-width: 768px)
	{
		max-width: 540px;
		padding-right: 15px;
		padding-left: 15px;
	}

	@media screen and (max-width: 540px)
	{
		max-width: 100%;
	}
}
