.articles-tabs
{
	display: flex;
	flex-wrap: wrap;

	margin-bottom: 60px;

	&__btn
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 400;
		line-height: 28px;

		flex-grow: 1;

		margin-top: -1px;
		margin-left: -1px;
		padding-top: 9px;
		padding-bottom: 9px;

		letter-spacing: .28px;
		text-transform: uppercase;

		color: $color-dark-gray;
		border: 1px solid #d6d6d6;
		background-color: $color-white;

		@media screen and (max-width: 540px)
		{
			font-size: 11px;
		}

		&.active
		{
			font-weight: 700;

			background-color: #d6d6d6;
		}
	}
}
