.more-servies
{
	padding-top: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
	}

	&__inner
	{
		display: flex;
		align-items: center;
		flex-direction: column;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		max-width: 830px;
		margin-bottom: 50px;
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 28px;
			line-height: 34px;
		}
	}

	&__list
	{
		position: relative;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;
	}

	&__item
	{
		position: relative;

		max-width: 310px;
		margin-bottom: 30px;

		cursor: pointer;

		@media screen and (max-width: 1440px)
		{
			width: 24%;
		}

		@media screen and (max-width: 1200px)
		{
			width: 32%;
		}

		@media screen and (max-width: 992px)
		{
			width: 49%;
			max-width: 100%;
		}

		@media screen and (max-width: 768px)
		{
			width: 100%;
		}

		& img
		{
			display: block;

			width: 100%;
			height: auto;

			border-radius: 10px;
		}

		& figcaption
		{
			font-family: $font-open-sans;
			font-size: 18px;
			font-weight: 700;
			line-height: 24px;

			margin-bottom: 10px;

			text-align: center;

			color: $color-white;
		}

		& ul
		{
			display: none;

			margin-top: 0;
			margin-bottom: 0;
			padding-left: 0;

			list-style: none;
		}

		& li
		{
			font-family: $font-open-sans;
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;

			text-align: center;

			color: $color-white;
		}
	}

	&__wrapper
	{
		position: absolute;
		z-index: 1000;
		bottom: 0;
		left: 50%;

		width: 100%;
		padding-top: 30px;
		padding-right: 10px;
		padding-bottom: 30px;
		padding-left: 10px;

		transform: translateX(-50%);

		border-radius: 10px;
		background-color: $color-orange;

		@media screen and (max-width: 1440px)
		{
			padding: 10px;
		}
	}

	&__item:hover &__wrapper
	{
		width: 110%;
	}

	&__item:hover ul
	{
		display: block;
	}
}
