.page-header
{
	background-image: url("../images/page-header-bg.jpg");
	background-repeat: no-repeat;
	background-position: 0 0;
	background-size: cover;
}

.page-wrapper
{
	padding-top: 100px;
	padding-bottom: 100px;

	background-color: #f7f7f7;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
		padding-bottom: 50px;
	}
}

.page
{
	&__servies
	{
		.preview__title
		{
			max-width: 515px;
		}

		.preview__text
		{
			max-width: 700px;
		}
	}

	&__news
	{
		.preview__title
		{
			margin-bottom: 0;
		}
	}
}
