.production
{
	padding-top: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
	}

	&__inner.container
	{
		@media screen and (min-width: 1440px)
		{
			max-width: 1085px;
		}
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 26px;
			line-height: 30px;
		}
	}

	&__list
	{
		display: flex;
		align-items: flex-start;
		justify-content: space-between;

		@media screen and (max-width: 992px)
		{
			flex-direction: column;
		}
	}

	&__item
	{
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-between;

		width: 100%;
		max-width: 525px;

		@media screen and (max-width: 1440px)
		{
			max-width: 465px;
		}

		@media screen and (max-width: 992px)
		{
			max-width: 100%;
			margin-bottom: 20px;
		}
	}

	&__image
	{
		display: block;

		width: 100%;
		height: auto;
		margin-bottom: 20px;
	}

	&__item-title
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 700;
		line-height: 24px;

		padding-left: 20px;

		text-transform: uppercase;

		color: $color-light-gray;

		@media screen and (max-width: 992px)
		{
			width: 100%;
			margin-bottom: 10px;
			padding-left: 0;
		}
	}
}
