.laboratory-indicators
{
	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& .title
	{
		align-self: center;

		max-width: 1180px;

		text-align: center;

		@media screen and (max-width: 992px)
		{
			font-size: 34px;
			line-height: 40px;
		}

		@media screen and (max-width: 540px)
		{
			font-size: 28px;
			line-height: 32px;
		}
	}

	&__wrapper
	{
		display: flex;
		justify-content: space-between;

		color: $color-white;

		@media screen and (max-width: 992px)
		{
			flex-direction: column;
		}
	}

	&__list
	{
		width: 100%;
		max-width: 655px;
		margin-top: 0;
		margin-bottom: 0;
		padding-top: 50px;
		padding-right: 100px;
		padding-bottom: 50px;
		padding-left: 0;
		padding-left: 100px;

		list-style: none;

		border-radius: 10px;
		background-color: $color-orange;

		@media screen and (max-width: 1440px)
		{
			max-width: 565px;
			padding: 70px;
		}

		@media screen and (max-width: 1200px)
		{
			max-width: 475px;
			padding: 50px;
		}

		@media screen and (max-width: 992px)
		{
			max-width: 100%;
			margin-bottom: 20px;
		}

		@media screen and (max-width: 992px)
		{
			padding: 25px;
		}
	}

	&__title
	{
		font-family: $font-open-sans;
		font-size: 20px;
		font-weight: 700;
		line-height: 28px;

		margin-top: 0;

		letter-spacing: .8px;
		text-transform: uppercase;
	}

	&__item
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;

		position: relative;

		margin-bottom: 10px;
		padding-left: 23px;

		color: rgba($color-white, .8);

		&::before
		{
			position: absolute;
			top: 4px;
			left: 0;

			width: 16px;
			height: 16px;
			padding: 3px;

			content: "";

			border: 1px solid $color-white;
			border-radius: 50%;
			background-color: $color-white;
			background-clip: content-box;
		}
	}
}
