.bread-crumbs
{
	display: flex;
	align-self: center;

	&__item
	{
		font-family: $font-montserrat;
		font-size: 14px;
		font-weight: 400;
		line-height: 28px;

		margin-right: 10px;
		margin-left: 10px;

		text-decoration: none;
		text-transform: uppercase;

		color: $color-light-gray;

		&.active
		{
			font-weight: 900;

			color: $color-orange;
		}
	}
}
