.arrows
{
	position: absolute;
	top: 50%;
	left: 50%;

	display: flex;
	justify-content: space-between;

	width: 100%;

	transform: translate(-50%, -50%);

	&__item
	{
		width: 26px;
		height: 42px;

		border: none;
		background-color: transparent;
		background-image: url("../images/arrow.png");

		&--prev
		{
			transform: translateX(-200%);
		}

		&--next
		{
			transform: translateX(200%) rotate(180deg);
		}
	}
}
