.products
{
	padding-top: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
	}

	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		align-self: center;

		max-width: 1100px;
		margin-bottom: 50px;
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 24px;
			line-height: 30px;
		}
	}

	&__list
	{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__item
	{
		position: relative;

		display: flex;
		overflow: hidden;
		flex-direction: column;

		max-width: 320px;
		margin-bottom: 20px;

		border-radius: 5px;
		background-color: $color-orange;

		@media screen and (max-width: 1440px)
		{
			max-width: 280px;
		}

		@media screen and (max-width: 1200px)
		{
			max-width: 230px;
		}

		@media screen and (max-width: 992px)
		{
			width: 49%;
			max-width: 350px;
			margin-right: auto;
			margin-left: auto;
		}

		@media screen and (max-width: 540px)
		{
			width: 100%;
			max-width: 100%;
		}

		&:nth-last-child(-n+3)
		{
			max-width: 435px;

			@media screen and (max-width: 1440px)
			{
				max-width: 375px;
			}

			@media screen and (max-width: 1200px)
			{
				max-width: 310px;
			}

			@media screen and (max-width: 992px)
			{
				width: 49%;
				max-width: 350px;
			}

			@media screen and (max-width: 540px)
			{
				width: 100%;
				max-width: 100%;
			}
		}
	}

	&__image
	{
		display: block;

		width: 100%;
		height: auto;
	}

	&__item-title
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 700;
		line-height: 24px;

		display: block;

		width: 100%;
		padding-top: 25px;
		padding-right: 60px;
		padding-bottom: 25px;
		padding-left: 60px;

		text-align: center;
		text-transform: uppercase;

		color: $color-white;

		@media screen and (max-width: 1440px)
		{
			padding: 15px;
		}
	}
}
