.footer
{
	padding-top: 65px;
	padding-bottom: 50px;

	background-color: #f7f7f7;

	&__inner
	{
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	& .menu
	{
		@media screen and (max-width: 1200px)
		{
			&__list
			{
				flex-direction: column;

				border-bottom: none;
			}

			&__item
			{
				margin-right: 0;
				margin-left: 0;
				padding-top: 5px;
				padding-bottom: 5px;
			}
		}

		@media screen and (max-width: 540px)
		{
			display: none;
		}
	}
}
