.conditions
{
	padding-top: 100px;

	@media screen and (max-width: 540px)
	{
		padding-top: 50px;
	}

	&__inner
	{
		display: flex;
		flex-direction: column;
	}

	& .title,
	& .text
	{
		text-align: center;
	}

	& .text
	{
		align-self: center;

		max-width: 800px;
		margin-bottom: 40px;
	}

	& .title
	{
		@media screen and (max-width: 540px)
		{
			font-size: 34px;
			line-height: 40px;
		}
	}

	&__list
	{
		display: flex;
		justify-content: space-between;

		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;

		color: $color-white;

		@media screen and (max-width: 1200px)
		{
			align-items: center;
			flex-direction: column;
		}
	}

	&__item
	{
		position: relative;

		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;

		max-width: 425px;
		min-height: 435px;

		cursor: pointer;

		border-radius: 5px;
		background-color: $color-dark-gray;

		@media screen and (max-width: 1440px)
		{
			max-width: 365px;
		}

		@media screen and (max-width: 1200px)
		{
			width: 100%;
			max-width: 425px;
			margin-bottom: 20px;
		}

		@media screen and (max-width: 540px)
		{
			align-self: center;
			justify-content: center;

			&:hover .conditions__title
			{
				display: none;
			}

			& .conditions__desc
			{
				position: relative;
				bottom: auto;
				left: auto;
			}
		}

		&:nth-child(1)
		{
			background-image: url("../images/conditions/1.jpg");
		}

		&:nth-child(2)
		{
			background-image: url("../images/conditions/2.jpg");
		}

		&:nth-child(3)
		{
			background-image: url("../images/conditions/3.jpg");
		}
	}

	&__title
	{
		font-family: $font-open-sans;
		font-size: 20px;
		font-weight: 700;

		padding-right: 50px;
		padding-left: 50px;

		text-align: center;
		letter-spacing: .8px;
	}

	&__desc
	{
		font-family: $font-open-sans;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;

		position: absolute;
		bottom: 0;
		left: 0;

		display: none;

		width: 100%;
		margin-top: 0;
		margin-bottom: 0;
		padding-right: 25px;
		padding-left: 25px;

		text-align: center;
	}

	&__item:hover &__desc
	{
		display: block;

		padding-bottom: 20px;
	}
}
